import React, { useState } from 'react';
import { animated } from 'react-spring';

import EventCard from './EventCard';

const EventRow = ({ item, itemKey, props, isDesktop, selectedGroup, numOfEventsInDay, openedCard, setOpenedCard }) => {

    const [checkedCard, setCheckedCard] = useState({groupNum: -1, cardNum: -1});
    
    return <animated.div key={itemKey} style={props}>
        <div key={item.id} className="ev-cards ev-font-padding">
            { item.data.map((ev, groupNum) => {
                        return (isDesktop || selectedGroup === groupNum) ? <EventCard
                            key={groupNum} 
                            timeIndex={item.id}
                            numOfEventsInDay={numOfEventsInDay}
                            eventName={ev.eventName}
                            lecturerName={ev.lecturerName}
                            time={ev.time}
                            openedCard={openedCard}
                            setOpenedCard={setOpenedCard}
                            groupNum={groupNum}
                            checkedCard={checkedCard}
                            setCheckedCard={setCheckedCard} /> : <div key={groupNum} className="hide" />
                    }) }
        </div>
    </animated.div>
}

export default EventRow;