const Event = ({ dayNum, eventDayOpened, transRef }) => {

    const [windowProperties, _] = useContext(WindowPropertiesContext);

    // const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [selectedGroup, setSelectedGroup] = useState(0);
    const [openedCard, setOpenedCard] = useState({groupNum: -1, cardNum: -1});

    // const updateWindowDimensions = () => {
    //     setWindowWidth(window.innerWidth);
    // }

    // useEffect(() => {
    //     window.addEventListener('resize', updateWindowDimensions);
    //     return () => {
    //         window.removeEventListener('resize', updateWindowDimensions);
    //     }
    // });

    const handleRightSwipe = () => {
        if (dayNum > 0) {
            setSelectedGroup(selectedGroup - 1);
        }
    }

    const handleLeftSwipe = () => {
        if (dayNum < 2) {
            setSelectedGroup(selectedGroup + 1);
        }
    }

    let isDesktop = windowProperties.width > 800;

    let firstGroupLabelStyle = 'violet';
    let secondGroupLabelStyle = 'violet-blue';
    let thirdGroupLabelStyle = 'blue';

    if (!isDesktop) {
        if (selectedGroup === 0) {
            firstGroupLabelStyle = 'white pointer violet-bg padded-container';
            secondGroupLabelStyle += ' pointer padded-container';
            thirdGroupLabelStyle += ' pointer padded-container';
        } else if (selectedGroup === 1) {
            secondGroupLabelStyle = 'white pointer grad-bg padded-container';
            firstGroupLabelStyle += ' pointer padded-container';
            thirdGroupLabelStyle += ' pointer padded-container';
        } else {
            thirdGroupLabelStyle = 'white pointer blue-bg padded-container';
            firstGroupLabelStyle += ' pointer padded-container';
            secondGroupLabelStyle += ' pointer padded-container';
        }
    }

    const swipeHandler = useSwipeable({ onSwipedRight: (eventData) => handleRightSwipe(), onSwipedLeft: (eventData) => handleLeftSwipe() });

    const dayTransitions = useTransition(eventDayOpened === dayNum ? events[dayNum].lectures : [], item => item.id, {
        ref: transRef,
        unique: true,
        trail: 100,
        // config: config.stiff,
        config: (item, state) => {
            switch (state) {
              case "enter": return { duration: 200 }
            //   case "update": return { duration: 300 }
              case "leave": return { duration: 200 }
            }
        },
        from: { transform: 'scale(0)', position: 'absolute' },
        enter: { transform: 'scale(1)', position: 'static' },
        leave: { transform: 'scale(0)' }
        // leave: item => async (next, cancel) => {
        //     // await next({opacity: 0})
        //     await next({transform: 'scale(0)'})
        //     await next({position: 'absolute'})
        //   },
    });

    return (
        <div className="relative">
            { eventDayOpened === dayNum ? dayTransitions.map(({ item, key, props }) => 
            <EventRow item={item}
                key={key}
                itemKey={key}
                props={props}
                isDesktop={isDesktop}
                selectedGroup={selectedGroup}
                numOfEventsInDay={events[dayNum].lectures.length}
                openedCard={openedCard}
                setOpenedCard={setOpenedCard} />) : dayTransitions.reverse().map(({ item, key, props }) =>
            <EventRow item={item}
                key={key}
                itemKey={key}
                props={props}
                isDesktop={isDesktop}
                selectedGroup={selectedGroup}
                numOfEventsInDay={events[dayNum].lectures.length}
                openedCard={openedCard}
                setOpenedCard={setOpenedCard} />)}
        </div>
    );

    return (
        <div className="events-container relative margin-from-prev-comp" {...swipeHandler}>
            <div className="flex-column">
                <div className="ev-cards ev-padding-label">
                    <div className={"group-label ev-right-margin text-centered " + firstGroupLabelStyle} onClick={() => setSelectedGroup(0)}>1 Поток</div>
                    <div className={"group-label ev-right-margin text-centered " + secondGroupLabelStyle} onClick={() => setSelectedGroup(1)}>2 Поток</div>
                    <div className={"group-label ev-right-margin text-centered " + thirdGroupLabelStyle} onClick={() => setSelectedGroup(2)}>3 Поток</div>
                </div>
                { events[dayNum].lectures.map((itemList, timeIndex) => 
                    <div key={timeIndex} className="ev-cards ev-font-padding">
                        { itemList.map((item, groupNum) => {
                            return (isDesktop || selectedGroup === groupNum) ? <EventCard
                                key={groupNum} 
                                timeIndex={timeIndex}
                                numOfEventsInDay={events[dayNum].lectures.length}
                                eventName={item.eventName}
                                lecturerName={item.lecturerName}
                                time={item.time}
                                openedCard={openedCard}
                                setOpenedCard={setOpenedCard}
                                groupNum={groupNum}/> : <div key={groupNum} className="hide" />
                        }) }
                    </div>) }
            </div>
        </div>
    );
}

export default Event;